<template>
  <input
      :inputName="(inputName) ? inputName : null"
      :type="(inputType) ? inputType : null"
      :value="(inputValue) ? inputValue : null"
      :step="(inputType=='number' && inputStep) ? inputStep : null"
      :min="(inputType=='number' && inputMin) ? inputMin : null"
      :max="(inputType=='number' && inputMax) ? inputMax : null"
      :disabled="(inputDisabled) ? 'disabled' : null"
      :placeholder="(inputPlaceholder) ? inputPlaceholder : null"
      @change="emitChange"
      @keyup="emitChange"
  />
</template>

<script>
import { store } from "@/store/store";

export default {
  name: "AtomInput",
  props: {
    inputName: {
      type: String,
      required: true,
      default: "text"
    },
    inputType: {
      type: String,
      required: true,
      default: "text"
    },
    inputDisabled: {
      type: String,
      required: false
    },
    inputValue: {
      type: String,
      required: false
    },
    inputStep: {
      type: String,
      required: false
    },
    inputMin: {
      type: String,
      required: false
    },
    inputMax: {
      type: String,
      required: false
    },
    inputPlaceholder: {
      type: String,
      required: false
    }
  },
  data() {
    return {
      store: store
    }
  },
  created() {
      this.store.storeValue(this.inputName, this.inputValue || false)
  },
  methods: {
    emitChange(e) {
        this.store.storeValue(this.inputName, e.target.value)
    }
  }
}
</script>

