<script>

import {store} from "@/store/store";

export default class PayplugPaymentOneClick {

	name = "one_click";
	type = "checkbox"
	title = null;
	description = null;
	link_know_more = null;
	checked = null
	options = null;

	constructor(data)
	{
		const mode = store.manageMode();
		this.title = data.title;
		this.description = data.descriptions[mode].description;
		this.link_know_more = data.descriptions[mode].link_know_more;
		this.checked = data.checked;
		this.options = data.options;
	}

}

</script>

