<script>

import {store} from "@/store/store";

export default class PayplugStatus {
  title = null;
  error = null;
  description = null;
  errorMessage = null;
  check = null;
  requirements = null;

  constructor(data) {

    if (typeof data === "undefined")
      return

    const mode = store.manageMode();
    this.error = data.error;
    this.title = data.title;
    this.description = data.descriptions[mode].description;
    this.errorMessage = data.descriptions[mode].errorMessage;
    this.check = data.descriptions[mode].check;
    this.requirements = data.requirements;

  }

}

</script>
