<script>

import {store} from "@/store/store";

export default class PayplugStandardInputTitle{

	name = "standard_payment_title";
	type = "input";
	title = null;
	description = null;
	placeholder = null;
	options = null;
	value = null;

	constructor(data)
	{
		const mode = store.manageMode();
		this.title = data.title;
		this.value = data.value;
		this.description = data.descriptions[mode].description;
		this.placeholder = data.descriptions[mode].placeholder;
	}

}

</script>

