</template>

<script>
export default {
  name: "AtomAnchor",
  props: {
    anchorHref: {
      type: String,
      required: true,
    },
    anchorText: {
      type: String,
      required: true,
    },
    anchorTitle: {
      type: String,
      required: false,
    },
    anchorTarget: {
      type: String,
      required: false,
      default: "_blank"
    },
    anchorDisabled: {
      type: String,
      required: false,
    }
  }
}
</script>

