<script>

import {store} from "@/store/store";

export default class PayplugPaylater {
  title = null;

  constructor(data) {

    if (typeof data === "undefined")
      return

	store.data.enable_oney = data.options.checked

	const mode = store.manageMode();
	this.title = data.title;
	this.description = data.descriptions[mode].description;
	this.name = data.options.name;
	this.oney_title = data.options.title;
	this.oney_description = data.options.descriptions[mode].description;
	this.links = data.options.descriptions[mode].link_know_more ?? null;
	this.options = data.options.options;
	this.advanced_settings = data.options.descriptions.advanced.description;
	this.advanced_options = data.options.advanced_options;

  }

}

</script>
