<script>

import {store} from "@/store/store";
import PayplygStandard from "@/classes/PaymentMethods/PayplygStandard";
import PayplugBancontact from "@/classes/PaymentMethods/PayplugBancontact";
import PayplugApplePay from "@/classes/PaymentMethods/PayplugApplePay";
import PayplugAmericanExpress from "@/classes/PaymentMethods/PayplugAmericanExpress";

export default class PayplugPaymentMethods {

	title = null;
	image = null;
	description=null;
	payment_methods = [];

	constructor(data)
	{
		let that = this;
		const mode = store.manageMode();
		this.title = data.title;
		this.image = data.image;
		this.description = data.descriptions[mode].description;

		data.options.map(function(payment_method_data){
			const payment_method = handlePaymentMethod(payment_method_data);
			if(payment_method !== null){
				that.payment_methods.push(payment_method)

			}
		})

		function handlePaymentMethod(data) {
			switch (data.name){
				case 'standard':
					return new PayplygStandard(data);
					// eslint-disable-next-line no-unreachable
					break;
				case 'bancontact':
					return new PayplugBancontact(data);
					// eslint-disable-next-line no-unreachable
					break;
				case 'applepay':
					return new PayplugApplePay(data);
					// eslint-disable-next-line no-unreachable
					break;
				case 'american_express':
					return new PayplugAmericanExpress(data);
					// eslint-disable-next-line no-unreachable
					break;
				default:
					return null;
					// eslint-disable-next-line no-unreachable
					break;
			}
		}

	}
}

</script>
