<script>

import {store} from "@/store/store";

export default class PayplugLogged {
	title = null;
	image = null;
	description=null;
	logout=null;
	email=null;
	mode=null;
	mode_description=null;
	link_learn_more=null;
	link_access_portal=null;
	options = null;

	constructor(data, wooc_settings)
	{

		if(typeof data === "undefined")
			return

		const mode = store.manageMode();
		this.title = data.title;
		this.image = data.image;
		this.email = wooc_settings.email;
		this.description = data.descriptions[mode].description;
		this.logout = data.descriptions[mode].logout;
		this.mode = data.descriptions[mode].mode;
		this.mode_description = data.descriptions[mode].mode_description;
		this.link_learn_more = data.descriptions[mode].link_learn_more;
		this.link_access_portal = data.descriptions[mode].link_access_portal;

		this.options = data.options;
	}

}

</script>
