<script>

import {store} from "@/store/store";

export default class PayplugStatus {
	title = null;
	description = null;
	checked = null;
	name = "enable_debug_checked"

	constructor(data) {

		if (typeof data === "undefined")
			return

		const mode = store.manageMode();
		this.title = data.enable_debug_label;
		this.description = data.descriptions[mode].enable_debug_description;
		this.checked = data.descriptions[mode].enable_debug_checked;

	}

}

</script>
