<template>
  <div class="payplugUIAccordion">
    <input type="checkbox" :id="identifier">
    <label class="payplugUIAccordionLabel" :for="identifier" @click="show = !show">{{ accordionLabel }}</label>
    <div v-show="show" class="payplugUIAccordion_contentWrapper">
      <div v-if="accordionContent" class="payplugUIAccordion_content">{{ accordionContent }}</div>
      <div v-else class="payplugUIAccordion_content"><slot/></div>
    </div>
  </div>
</template>

<script>

export default {
  name: "MoleculeAccordion",
  data: () => {
    return {
      show: false,
      flag: false,
    }
  },
  props: {
    accordionLabel: {
      type: String,
      required: false,
      default: ""
    },
    accordionContent: {
      type: String,
      required: true,
      default: ""
    },
    accordionCollapsed: {
      type: Boolean,
      required: false,
      default: false
    },
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init() {
      if(this.accordionCollapsed && !this.flag) {
        this.show = this.accordionCollapsed
        this.flag = true
      }
    }
  }
}
</script>

