     :disabled="(buttonLinkDisabled) ? 'disabled': null">
    <template v-if="buttonLinkIcon">
      <Icon :iconName="buttonLinkIcon"/>
    </template>
    {{ buttonLinkText }}
  </a>
</template>

<script>
import Icon from "../../components/atoms/AtomIcon";

export default {
  name: "AtomButtonLink",
  components: {
    Icon
  },
  props: {
    buttonLinkHref: {
      type: String,
      required: true,
      default: ""
    },
    buttonLinkTitle: {
      type: String,
      required: false
    },
    buttonLinkText: {
      type: String,
      required: false
    },
    buttonLinkTarget: {
      type: String,
      required: false,
      default: "_blank"
    },
    buttonLinkClass: {
      type: String,
      required: false
    },
    buttonLinkIcon: {
      type: String,
      required: false
    },
    buttonLinkDisabled: {
      type: Boolean,
      required: false
    },
  }
}
