import { reactive } from 'vue'

export const store = reactive({
    // all data that will be used for the different requests
    settings: {
		environment: "sandbox",
		logged: false,
		standard_payment_mode: null,
		loading: {
			global:true,
			login:false,
			logout: false,
			status: false,
			payment_methods:{
				bancontact: false,
				applepay: false,
				american_express: false,
			}
		},
		mode: false,	//false=test || true=live
        ajax_url: window.payplug_admin_config ? window.payplug_admin_config.ajax_url : "/wp-admin/admin-ajax.php",
        "nonce": ""
    },

	//data added on error messages
	error:{},

	// data needed for requests
    data: {},

    storeValue(name, value, action = "data") {
        this[action][name] = value
    },

	manageMode(){
		if(this.settings.mode === true || this.settings.mode === 1 || this.settings.mode === "1"){
			return this.settings.environment = 'live';
		}

		return this.settings.environment = 'sandbox';
	},

	translations:{
		header:{},
		login:{},
		subscribe:{},
		logged:{},
		payment_paylater:{},
		payment_methods:{},
		status: {},
		debug: {}
	},
	rawData:{}
})
