<script>

import {store} from "@/store/store";

export default class PayplugSubscribe {
	title = null;
	name = null;
	description=null;
	link_create_account = null;
	content_description = null;
	already_have_account = null;

	constructor(data)
	{

		if(typeof data === "undefined")
			return

		const mode = store.manageMode();
		this.title = data.title;
		this.name = data.name;
		this.description = data.descriptions[mode].description;
		this.link_create_account = data.descriptions[mode].link_create_account;
		this.content_description = data.descriptions[mode].content_description;
		this.already_have_account = data.descriptions[mode].already_have_account;
	}

}

</script>
