<template>
  <section class="payplugUiBlock paymentMethodBlock " data-e2e-name="paymentMethodsBlock">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content">
      <div class="paymentOption" :class="'-' + payment_method.name" v-for="(payment_method, index) in payment_methods" :key="payment_method">
        <PMethodFull :payment-method="payment_method" :index="index" />
      </div>
    </div>
	<Modal ref="errorModal" :modal-content="error_message.msg" :modal-title="error_message.title" :modal-close="error_message.close"></Modal>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import PMethodFull from "./PMethod/OrganismPMethodFull";
import {store} from "@/store/store";
import Modal from "@/components/molecules/MoleculeModal";

export default {
	name: "OrganismSectionPMethods",
	components:{ BlockHeader, PMethodFull, Modal },
	computed:{
		title: {
			get: () => store.translations.payment_methods.title
		},
		description:{
			get: () => store.translations.payment_methods.description
		},
		payment_methods:{
			get: () => store.translations.payment_methods.payment_methods
		},
		error_message(){
			let error_obj = store.error.payment_methods;

			if( typeof error_obj !== "undefined" && error_obj !== null )
				this.showError();

			if(typeof error_obj === "undefined")
				error_obj = {};

			if(typeof error_obj.msg === "undefined")
				error_obj.msg = {};

			if(typeof error_obj.title === "undefined")
				error_obj.title = {};

			if(typeof error_obj.close === "undefined")
				error_obj.close = {};

			return error_obj;
		},
	},
	methods:{
		showError(){
			this.$refs.errorModal.showModal();
		},
		closeModlal() {
			this.$refs.errorModal.closeModal();
		}
	}
}
</script>

