<template>
  <select class="payplugUISelect" :name="selectName"
          :disabled="(selectDisabled) ? 'disabled' : null"
          @change="store.storeValue(selectName, $event.target.value)">
    <option v-for="option in selectOptions"
            v-bind:key="option"
            :value="option.value"
            :selected="(option?.checked) ? 'selected' : null"
            :disabled="(option?.disabled) ? 'disabled' : null"
            :data-e2e-name="selectName + '-' + option.value">
      {{ option.label }}
    </option>
  </select>
</template>

<script>
import { store } from "@/store/store";

export default {
    name: "AtomSelect",
    props:{
      selectName: {
        type: String,
        required: true
      },
      selectOptions: {
        type: Object,
        required: true
      },
      selectDisabled: {
        type: Boolean,
        required: false
      }
    },
    data() {
      return {
        store: store
      }
    },
    created() {
		this.populate();
    },
	updated() {
		this.populate();
	},
	methods:{
		populate(){
			let selected = "";
			this.selectOptions.map(option => {
				if(option.checked)
					selected = option.value
			})
			this.store.storeValue(this.selectName, selected)
		}
	}
  }
</script>


