var qs = require('qs');
import axios from "axios";
import {store} from "@/store/store";
import PayplugHeader from "../classes/PayplugHeader";
import PayplugLogin from "../classes/PayplugLogin";
import PayplugSubscribe from "../classes/PayplugSubscribe";
import PayplugLogged from "../classes/PayplugLogged";
import PayplugPaylater from "../classes/PayplugPaylater";
import PayplugPaymentMethods from "../classes/PayplugPaymentMethods";
import PayplugStatus from "../classes/PayplugStatus";
import PayplugDebugMode from "../classes/PayplugDebugMode";

export async function $post(url, data) {
	// eslint-disable-next-line no-useless-catch
	try {
		return axios.post(url,  data );

	} catch (error) {
		throw error;
	}
}

export function $init(){

	$post(
		store.settings.ajax_url,
		'action=payplug_init'

	).then( (response) => {
		store.settings.logged = response.data.data.settings.logged;
		store.settings.mode = response.data.data.settings.mode;
		populate(response.data.data);

		store.rawData = response.data.data;
	})
	.catch(error => {
		//commit('setError', error);
		console.log(error);
	})
	.finally(function(){
		//commit('setLoading', false);
		store.settings.loading.global = false;

	})
}

export function $login() {
	//TODO:: missing NONCE

	let data = {
		"action": "payplug_login",
		"payplug_email": store.data.payplug_email,
		"payplug_password": store.data.payplug_password
	};

	store.error.login = null;

	$post(
		store.settings.ajax_url,
		qs.stringify(data)

	).then( (response) => {
		store.settings.logged = response.data.data.settings.logged;
		store.settings.mode = response.data.data.settings.mode;
		populate(response.data.data);
		store.rawData = response.data.data;

	}).catch(function (error) {
		const { response } = error;
		const { ...errorObject } = response; // take everything but 'request'
		store.error.login = errorObject.data.data.message;

	}).finally(function(){
		store.settings.loading.login = false;
	})

}

export function $logout(){

	let data = {"action": "payplug_logout",};
	$post(
		store.settings.ajax_url,
		qs.stringify(data)

	).then( (response) => {
		console.log(response);

	}).catch(function (error) {
		console.log(error);

	}).finally(function(){
		store.settings.logged = false;
		store.settings.loading.logout = false;
	})

}

export function $enableBancontact(status){
	if(status === false){
		return {success:false};
	}

	return request_payment_method_permissions("api_check_bancontact_permissions")

}

export function $enableApplePay(status){
	if(status === false){
		return {success:false};
	}

	return request_payment_method_permissions("api_check_applepay_permissions")

}

export function $enableAmericanExpress(status){
	if(status === false){
		return {success:false};
	}

	return request_payment_method_permissions("api_check_american_express_permissions")

}

export function $enableOney(status){

	if(status === false)
		return {success:false};


	store.settings.loading.paylater = true;
	return request_payment_method_permissions("api_check_oney_permissions").finally(function(){
		store.settings.loading.paylater = false;
	})

}

export function $check_requirements(){

	let data = {"action": "payplug_check_requirements",};
	$post(
		store.settings.ajax_url,
		qs.stringify(data)

	).then( (response) => {
		populate_status(response.data.data);

	}).catch(function (error) {
		console.log(error);

	}).finally(function(){
		store.settings.loading.status = false;
	})

}

export function populate(data){
	store.translations.header = new PayplugHeader(data.header);
	store.translations.login = new PayplugLogin(data.login);
	store.translations.subscribe = new PayplugSubscribe(data.subscribe);
	store.translations.logged = new PayplugLogged(data.logged, data.payplug_wooc_settings);
	store.translations.payment_paylater = new PayplugPaylater(data.payment_paylater);
	store.translations.payment_methods = new PayplugPaymentMethods(data.payment_methods);
	store.translations.status = new PayplugStatus(data.status);
	store.translations.debug = new PayplugDebugMode(data.status);
}

export function populate_status(data){
	store.translations.status = new PayplugStatus(data.status);
}

function request_payment_method_permissions($payment_method){

	return new Promise((resolve, reject) => {
		$post(
			store.settings.ajax_url,
			qs.stringify({"action": $payment_method, "env":store.data.payplug_sandbox})
		)
			.then((response) => {
				resolve(response.data);

			})
			.catch(error => {
				reject(error);
				console.log(error);
			})
	});

}
