<template>
  <section class="payplugUiBlock stateBlock" data-e2e-name="blockState">
    <AtomLoader v-if="loading" class="component-wrapper" />
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content" v-bind:class = "(loading)?'blurred':''">
      <Accordion class="-stateBlock" :accordionCollapsed="error">
        <TextAlert v-if="error" :textAlertText="errorMessage" textAlertType="-warning"/>
        <div class="_stateContent">
          <PayplugButton :buttonText="check" class="-tertiary" @click.prevent="doCheck" />
          <div class="indicators">
            <Indicator v-for="(requirement, i) in requirements" :status="requirement.status" :text="requirement.text" v-bind:key="'indicator_' + i"/>
          </div>
        </div>
        <payplugUICheckbox />
      </Accordion>
    </div>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import Accordion from "../molecules/MoleculeAccordion";
import TextAlert from "../atoms/AtomTextAlert";
import PayplugButton from "../atoms/AtomButton";
import Indicator from "../molecules/MoleculeIndicator";
import payplugUICheckbox from "../molecules/MoleculeCheckbox";
import {store} from "@/store/store";
import AtomLoader from "@/components/atoms/AtomLoader";
import {$check_requirements} from "@/store/ajax";

export default {
  name: "OrganismSectionStatus",
  components:{
    Indicator, BlockHeader, Accordion, TextAlert, PayplugButton, payplugUICheckbox, AtomLoader
  },
  computed: {
    error: {
      get: () => store.translations.status.error
    },
    title: {
      get: () => store.translations.status.title
    },
    description: {
      get: () => store.translations.status.description
    },
    errorMessage: {
      get: () => store.translations.status.errorMessage
    },
    check: {
      get: () => store.translations.status.check
    },
    requirements: {
      get: () => store.translations.status.requirements
    },
    loading:{
      get: () => store.settings.loading.status
    }
  },
  methods: {
    doCheck() {
      store.settings.loading.status = true;
      $check_requirements();
    }
  }

}
</script>

