<template>
	<transition name="slide-fade">

	<div class="payplugUIModal" :id="identifier">
		<label class="_overlay" @click="closeModal"></label>
		<div class="_modal" :class="(modalTitle) ? '' : ' -noTitle '">
			<label for="modalTriggered" class="_close" @click="closeModal">
				<Icon iconName="close"/>
			</label>
			<div v-if="modalTitle" class="_header">{{modalTitle}}</div>
			<div v-if="modalContent" class="_body" v-html="modalContent"></div>
			<div class="_footer" v-if="modalClose.length > 0">
				<CloseButton :button-text="modalClose" v-on:click="closeModal"/>
			</div>
		</div>
	</div>
	</transition>
</template>

<script>
import Icon from "../atoms/AtomIcon";
import { v4 as uuidv4 } from 'uuid';
import CloseButton from "../atoms/AtomButton";

export default {
  name: "MoleculeModal",
  components: {Icon, CloseButton},
  props: {
    modalTitle: {
      type: String,
      required: false
    },
    modalContent: {
      type: String,
      required: true,
      default: ""
    },
	modalClose: {
		type: String,
		required: false,
		default: ""
	}
  },
  data: () => {
    return {
      identifier: uuidv4(),
    }
  },
  methods: {
    showModal() {
      document.getElementById(this.$data.identifier).classList.add("_show");
    },
    closeModal() {
      document.getElementById(this.$data.identifier).classList.remove("_show");
    }
  }
}
</script>

