<script>

import {store} from "@/store/store";

export default class PayplugEmbeded{

	name = "embeded";
	type = "radio"
	title = null;
	description = null;
	description_redirected = null;
	description_popup = null;
	link_know_more = null;
	options = null;

	constructor(data)
	{
		let that = this;
		const mode = store.manageMode();
		this.title = data.title;
		this.description_redirected = data.descriptions[mode].description_redirected;
		this.description_popup = data.descriptions[mode].description_popup;
		this.link_know_more = data.descriptions[mode].link_know_more;
		this.options = data.options;
		setDescription(data);

		function setDescription(data){
			const mode = store.manageMode();
			data.options.map(option => {
				const embeded_selected_option = option.value
				that.description = data.descriptions[mode]["description_"+embeded_selected_option];
			})
		}
	}

}

</script>

