<template>
  <section class="payplugUiBlock generalBlock -logged" data-e2e-name="generalLogged">
	<AtomLoader v-if="loading" class="component-wrapper" />
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <div class="_content" v-bind:class = "(loading)?'blurred':''">
      <div class="_user">
        <div class="_userInformations">
          {{ email }}
          <Tooltip data-e2e-name="payplugTooltipLogout">
            <payplugUIButton buttonName="logout" :buttonText="lougout"
                             @click.prevent="doLogout"
                             data-e2e-name="payplugLogout"/>
          </Tooltip>
        </div>
        <ButtonLink class="_userPortal -icon"
					:buttonLinkText="link_access_portal_text"
					:buttonLinkHref="link_access_portal_href"
        buttonLinkIcon="link" />
      </div>
      <div class="_sandbox">
        <Title class="_sandboxTitle" :titleText="mode"/>
        <ParagraphAnchors class="_sandboxDescription -test" :paragraphAnchorsText="mode_description" :paragraphAnchorsLinks="[link_learn_more]" paragraphAnchorsData="faq" />
        <payplugUIIOptions v-if="options" class="_sandboxRadioButton" :optionsItems="options" :optionsName="options[0].name" optionsData="sandbox" />
      </div>
    </div>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import Tooltip from "../molecules/MoleculeTooltip";
import payplugUIButton from "../atoms/AtomButton";
import ButtonLink from "../atoms/AtomButtonLink";
import Title from "../atoms/AtomTitle";
import Anchor from "../atoms/AtomAnchor";
import payplugUIIOptions from "../atoms/AtomOptions";
import ParagraphAnchors from "@/components/molecules/MoleculeParagraphAnchors";
import {store} from "@/store/store";
import AtomLoader from "@/components/atoms/AtomLoader"
import { $logout} from "@/store/ajax";

export default {
	name: "OrganismSectionLoggedIn",
	components: { payplugUIButton, BlockHeader, Tooltip, ButtonLink, Anchor, payplugUIIOptions, Title, ParagraphAnchors, AtomLoader },
	computed: {
		title: {
			get: () => store.translations.logged.title
		},
		description: {
			get: () => store.translations.logged.description
		},
		email: {
			get: () => store.translations.logged.email
		},
		lougout: {
			get: () => store.translations.logged.logout
		},
		link_access_portal_text: {
			get: () => store.translations.logged.link_access_portal.text
		},
		link_access_portal_href: {
			get: () => store.translations.logged.link_access_portal.url
		},
		mode: {
			get: () => store.translations.logged.mode
		},
		mode_description: {
			get: () => store.translations.logged.mode_description
		},
		link_learn_more: {
			get: () => store.translations.logged.link_learn_more
		},
		options: {
			get: () => store.translations.logged.options
		},
		loading:{
			get: () => store.settings.loading.logout
		}
	},
	methods: {
		doLogout(){
			store.settings.loading.logout = true;
			$logout();
		}
	}
}
</script>

