<script>

import {store} from "@/store/store";

export default class PayplugLogin {
	title = null;
	image = null;
	description=null;
	not_registered=null;
	connect=null;
	email_label=null;
	email_placeholder=null;
	password_label=null;
	password_placeholder=null;
	link_forgot_password=null;

	constructor(data)
	{
		if(typeof data === "undefined")
			return

		const mode = store.manageMode();
		this.title = data.title;
		this.image = data.image;
		this.description = data.descriptions[mode].description;
		this.not_registered = data.descriptions[mode].not_registered;
		this.connect = data.descriptions[mode].connect;
		this.email_label = data.descriptions[mode].email_label;
		this.email_placeholder = data.descriptions[mode].email_placeholder;
		this.password_label = data.descriptions[mode].password_label;
		this.password_placeholder = data.descriptions[mode].password_placeholder;
		this.link_forgot_password = data.descriptions[mode].link_forgot_password;
	}

}

</script>
