<template>
  <div class="payplugUIIOptions" :class="optionsClassName">
    <template v-for="item in optionsItems" :key="item">
      <label :class="item?.className + ' ' + (item?.disabled ? '-disabled' : '')" @click="updateOption(item)"
      :data-e2e-name="optionsData + item?.label.replaceAll(/\s/g, '')">
        <input type="radio" :name="optionsName" :value="item?.value"
            :checked="((optionsSelected && item?.value && optionsSelected == item?.value) || (item?.checked)) ? 'checked' : null"
            :disabled="(item?.disabled) ? 'disabled' : null"/>
        <span> {{ item?.label }} <span v-if="item?.subText">{{ item?.subText }}</span></span>
      </label>
    </template>
  </div>
</template>

<script>
import { store } from "@/store/store";
import { populate } from "@/store/ajax"

export default {
  name: "AtomOptions",
  props: {
    optionsItems: {
      type: Object,
      required: true
    },
    optionsName: {
      type: String,
      required: false
    },
    optionsClassName: {
      type: String,
      required: false,
      default: ""
    },
    optionsSelected: {
      type: String,
      required: false
    },
    optionsData: {
      type: String,
      required: false
    },
  },
  data() {
    return {
      store: store
    }
  },
  created() {
    let selected = "";
    if(this.optionsItems)
      this.optionsItems.map(option => {
        if(option.checked)
          selected = option.value
      })

    this.store.storeValue(this.optionsName, selected+"")
  },
	methods: {
		updateOption(item) {
			store.storeValue(this.optionsName, item.value);
			if(this.optionsName === "payplug_sandbox") {
				store.settings.mode = item.value;
				populate(store.rawData);
			}

			this.$emit('update_embeded_option', item.value)

		},
	}
}
</script>

