<script>

import {store} from "@/store/store";

export default class PayplugHeader {

	title = null;
	image = null;
	version = null;
	description=null;
	options = null;

	constructor(data)
	{
		const mode = store.manageMode();
		this.title = data.title;
		this.image = data.image;
		this.version = data.descriptions[mode].plugin_version;
		this.description = data.descriptions[mode].description;
		this.options = data.options;
	}

}

</script>
