<script>

import {store} from "@/store/store";

export default class PayplugStandardInputDescription{

	name = "standard_payment_description";
	type = "input";
	title = null;
	description = null;
	placeholder = null;
	options = null;

	constructor(data)
	{
		const mode = store.manageMode();
		this.title = data.title;
		this.description = data.descriptions[mode].description;
		this.placeholder = data.descriptions[mode].placeholder;

	}

}

</script>

