<template>
	<div class="payplugConfiguration">
		<div class="payplug">
			<br/><br/>
			<pre v-if="showStore" class="store">{{store.data}}</pre>
			<AtomLoader v-if="loading" class="global-wrapper" />
			<div v-bind:class = "(loading)?'blurred':''">
				<Header />
				<Login v-if="!logged && !register" v-on:notRegisted="updateparent" v-on:showLoggedIn="showLoggedIn" />
				<Subscribe v-if="!logged && register" v-on:notRegisted="updateparent" />
				<LoggedIn v-bind:class="{ '-disabled' : plugin_disabled }" v-if="logged" v-on:showLogin="showLogin" />
				<PMethods v-bind:class="{ '-disabled' : !logged || plugin_disabled }" />
				<Paylater :environment="store.settings.environment" v-bind:class="{ '-disabled' : !logged || plugin_disabled }" />
				<Status :environment="store.settings.environment" v-bind:class="{ '-disabled' : !logged || plugin_disabled }" />
				<payplugUIButton class="saveChangesButton" buttonText="Save changes" @click="saveData"  v-bind:class="{ '-disabled' : !logged}" data-e2e-name="saveConfiguration"/>
			</div>
		</div>
	</div>
</template>

<script>
import Header from "@/components/organisms/OrganismSectionHeader";
import Login from "@/components/organisms/OrganismSectionLogin";
import Subscribe from "@/components/organisms/OrganismSectionSubscribe";
import LoggedIn from "@/components/organisms/OrganismSectionLoggedIn";
import Paylater from "@/components/organisms/OrganismSectionPaylater";
import Status from "@/components/organisms/OrganismSectionStatus";
import PMethods from "@/components/organisms/OrganismSectionPMethods";
import payplugUIButton from "@/components/atoms/AtomButton";
import AtomLoader from "@/components/atoms/AtomLoader";
import { store } from "@/store/store";
import {$init} from "@/store/ajax";

export default {
	computed:{
		plugin_disabled: {
			get: () => parseInt(store.data.payplug_enable) != 1
		},
		logged: {
			get: () => store.settings.logged
		},
		loading:{
			get: () => store.settings.loading.global
		}
	},
	name: "app",
	components:{ Header, Login, Subscribe, LoggedIn, Status, PMethods, Paylater, payplugUIButton, AtomLoader },
	data(){
		return {
			store: store,
			register: false,
			showStore: false,
		}
	},
	mounted() {
		$init();
	},
	methods: {
		updateparent(status) {
			this.register = status
		},
		showLoggedIn() {
			this.logged = true
		},
		showLogin() {
			this.logged = false
		},
		saveData() {
		}
	}
}
</script>

