<template>
  <div v-bind:class=" (imgParentClass) ? imgParentClass : null" >
    <img :src="img" :alt="imageAlt">
  </div>
</template>

<script>

export default {
  name: "AtomImage",
  props: {
    imgParentClass:  {
      type: String,
      required: false,
    },
    imageSrc: {
      type: String,
      required: true,
      default: ""
    },
    imageAlt: {
      type: String,
      required: false,
      default: ""
    },
  },
  data() {
    return {
      img: require("../../assets/img/" + ( this.imageSrc || "placeholder.png"))
    }
  },
}
</script>

